// # [GENERAL MIXINS]

@mixin wight($w, $h) {
	width: $w;
	height: $h;
}

@mixin max-wight($w, $h) {
	max-width: $w;
	max-height: $h;
}

@mixin min-wight($w, $h) {
	min-width: $w;
	min-height: $h;
}

@mixin mar-pad($m, $p) {
	margin: $m;
	padding: $p;
}

@mixin flex-center {
	display: flex;
	justify-content: center;
	align-items: center;
}

@mixin uni-select($toggle: true) {
	@if $toggle {
		user-select: text; // Standard syntax
		-ms-user-select: text; // IE 10 and IE 11
		-webkit-user-select: text; // Safari
	} @else {
		user-select: none;
		-ms-user-select: none;
		-webkit-user-select: none;
	}
}

@mixin shadow {
	box-shadow: $box-shadow-values $shadow-color;
	-webkit-box-shadow: $box-shadow-values $shadow-color;
	-moz-box-shadow: $box-shadow-values $shadow-color;
}

@mixin focus-style {
	&:focus,
	&:focus-within {
		outline: none;
	}

	&:focus-visible {
		outline: 3px solid $primary;
		outline-offset: 3px;
		transition: none;
		border-radius: $global-border-radius;
		opacity: 1;
		-moz-outline-radius: $global-border-radius;

		&::after {
			fill: $primary;
		}
	}
}

@mixin transition-anim {
	transition: 0.3s;
}

@mixin card {
	@include shadow;
	@include transition-anim;

	background-color: $background-accent;
	border-radius: $global-border-radius;
}

