// # [COMPONENT BOOTSTRAP]

// --[ BUTTON ]--

.primary {
	background-color: $primary;
}

.secondary {
	background-color: $secondary;
}

.success {
	background-color: $success;
}

.info {
	background-color: $info;
}

.warn {
	background-color: $warn;
}

.danger {
	background-color: $danger;
}

// --[ CARD ]--

.card {
	@include card();
}

// # [ROOT]

html,
body {
	@include uni-select(false);
	@include mar-pad(0, 0);
	@include wight(100vw, 100vh);

	font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
	color: $color;
	background: rgb(0,0,0);
	background: linear-gradient(0deg, rgba(0,0,0,1) 0%, rgba(0,5,24,1) 47%, rgba(19,34,116,1) 47.2%, rgba(0,14,29,1) 47.5%, rgba(0,0,0,1) 100%);
	overflow: hidden;
}

#root {
	@include wight(100%, 100%);
	@include flex-center;
}

a {
	@include focus-style();
	@include transition-anim;

	text-decoration: none;
	font-weight: bold;
	color: $color;

	&:hover {
		color: $primary;
		filter: brightness(95%);
	}

	&:active {
		color: $primary;
		filter: brightness(110%);
	}
}

svg {
	fill: $color;
}

// INTERFACE
.snap {
	scroll-snap-align: center;
}

h1 {
	font-size: 132px;
}

h2 {
	font-size: 62px;
}

h1,
h2,
h3,
h4,
h5,
h6 {
	margin: 0;
}

// # [SCROLLBARS]

$_thumb: $tone1;
$_track: $background-color;

// Chrome, Edge, Firefox, and Safari
* {
	scroll-behavior: smooth;

	&::-webkit-scrollbar {
		width: 15px;
		width: 0px;
	}
	&::-webkit-scrollbar-track {
		background: $_track;
	}
	&::-webkit-scrollbar-thumb {
		background-color: $_thumb;
		border-radius: 10px;
		border: 3px solid $_track;
	}
	&::-webkit-scrollbar-corner {
		background-color: $_thumb;
	}
}

@media only screen and (max-width: 430px) {
	html,
	body {
		overflow-y: scroll;
	}
}